<template>
    <van-nav-bar
      title="血糖"
      right-text="历史记录"
      fixed="true"
      left-arrow
      @click-left="back"
      @click-right="push({name: 'history', query:{type: 6}})"
  />
   <HomeDetial :firstCardData="state.firstCardData" :list="state.list"/>
</template>


<script setup >
    import { onBeforeMount, reactive } from "vue";
    import { formatYYMMDD } from '../../../utils/util';
    import { getHomeDetialHttp } from '../common/serviec';
    import HomeDetial from "../common/components/home-detial"
    import moment from "moment";
    import { useRouter } from "vue-router";
    let {push, back} = useRouter()
    let state = reactive({
       firstCardData: {
         icon: require('@/assets/house/common/celiang@2x.png'),
         title: '最近一次测量',
         rightText:'', //测量事假
         data : [
            {
            label: '血糖',
            value: 0,
            key: "bloodsugar",
            unit: '%',
            standard: "3.9～6.1"
            },
           {
             label: '类型',
             value: '0',
             key: "type",
             unit: '',
           }
         ]
       },
      list:[
        {
          icon: require('@/assets/house/common/shiwu@2x.png'),
          title: '食物建议',
          key: "foodSuggestion",
          rightText:'', //测量事假
          content: '暂无数据',
        },
        {
          icon: require('@/assets/house/common/yundong@2x.png'),
          title: '运动建议',
          key: "sportSuggestion",
          rightText:'', //测量事假
          content: '暂无数据',
        },
        {
          icon: require('@/assets/house/common/changgui@2x.png'),
          title: '常规建议',
          key: "commonSuggestion",
          rightText:'', //测量事假
          content: '暂无数据',
        },
        {
          icon: require('@/assets/house/common/yixue@2x.png'),
          title: '医学建议',
          key: "doctorSuggestion",
          rightText:'', //测量事假
          content: '暂无数据',
        }
      ]
    })
    let getHumanBodyData = async () => {
        try {
            let enumJson = {
                1: "空腹",
                2: "餐前", 
                3: "餐后",
                4: "随机"
            }
            let res = await getHomeDetialHttp(6, 2, formatYYMMDD(new Date), formatYYMMDD(new Date), 0, 0);
            console.log("【获取血糖】：", res)
            if(res.code !== 200) throw new Error(res.msg)
            state.firstCardData = {
                ...state.firstCardData,
                data: state.firstCardData.data.map(item => ({
                    ...item, 
                    value: item.key === "type" ? enumJson[res.data.object[0][item.key]] : res.data.object[0][item.key]
                })),
                rightText: moment(res.data.object[0].time).format("YYYY-MM-DD HH:mm:ss")
            }
             res.data.suggestion && (state.list = state.list.map(item => ({...item, content: res.data.suggestion[item.key]})));
        } catch (error) {
            console.error("【获取血糖失败】：", error)
        }
    };
    onBeforeMount(getHumanBodyData);
</script>